        </el-form-item>
        <el-form-item :style='{"width":"50%","margin":"0 0 20px 0"}'   v-if="flag=='yongcheren'"  label="" prop="shenfenzheng">
          <el-input v-model="ruleForm.shenfenzheng"               placeholder="" clearable></el-input>
        </el-form-item>
        <el-form-item :style='{"width":"50%","margin":"0 0 20px 0"}'   v-if="flag=='yongcheren'"  label="" prop="shouji">
          <el-input v-model="ruleForm.shouji"               placeholder="" clearable></el-input>
        </el-form-item>
        <el-form-item :style='{"width":"50%","margin":"0 0 20px 0"}'   v-if="flag=='yongcheren'"  label="" prop="youxiang">
          <el-input v-model="ruleForm.youxiang"               placeholder="" clearable></el-input>
        </el-form-item>
        <el-form-item :style='{"width":"50%","margin":"0 0 20px 0"}' v-if="flag=='yongcheren'" label="" prop="shenfenzhengzhao">
          <file-upload
