          :fileUrls="ruleForm.shenfenzhengzhao?ruleForm.shenfenzhengzhao:''"
          @change="yongcherenshenfenzhengzhaoUploadChange"
          ></file-upload>
        </el-form-item>
        <el-form-item :style='{"width":"50%","margin":"0 0 20px 0"}' v-if="flag=='yongcheren'" label="" prop="jiazhao">
          <file-upload
          tip=""
          action="file/upload"
          :limit="3"
          :multiple="true"
          :fileUrls="ruleForm.jiazhao?ruleForm.jiazhao:''"
          @change="yongcherenjiazhaoUploadChange"
          ></file-upload>
        </el-form-item>
        <el-form-item :style='{"width":"50%","margin":"0 0 20px 0"}'   v-if="flag=='gongcheren'"  label="" prop="zhanghao">
          <el-input v-model="ruleForm.zhanghao" readonly              placeholder="" clearable></el-input>
        </el-form-item>
        <el-form-item :style='{"width":"50%","margin":"0 0 20px 0"}'   v-if="flag=='gongcheren'"  label="" prop="xingming">
          <el-input v-model="ruleForm.xingming"               placeholder="" clearable></el-input>
        </el-form-item>
        <el-form-item :style='{"width":"50%","margin":"0 0 20px 0"}' v-if="flag=='gongcheren'"  label="" prop="xingbie">
          <el-select v-model="ruleForm.xingbie"  placeholder="">
            <el-option
                v-for="(item,index) in gongcherenxingbieOptions"
                v-bind:key="index"
                :label="item"
                :value="item">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item :style='{"width":"50%","margin":"0 0 20px 0"}' v-if="flag=='gongcheren'" label="" prop="touxiang">
          <file-upload
          tip=""
          action="file/upload"
          :limit="3"
          :multiple="true"
          :fileUrls="ruleForm.touxiang?ruleForm.touxiang:''"
          @change="gongcherentouxiangUploadChange"
          ></file-upload>
        </el-form-item>
        <el-form-item :style='{"width":"50%","margin":"0 0 20px 0"}'   v-if="flag=='gongcheren'"  label="" prop="nianling">
          <el-input v-model="ruleForm.nianling"               placeholder="" clearable></el-input>
        </el-form-item>
        <el-form-item :style='{"width":"50%","margin":"0 0 20px 0"}'   v-if="flag=='gongcheren'"  label="" prop="shenfenzheng">
          <el-input v-model="ruleForm.shenfenzheng"               placeholder="" clearable></el-input>
        </el-form-item>
        <el-form-item :style='{"width":"50%","margin":"0 0 20px 0"}'   v-if="flag=='gongcheren'"  label="" prop="youxiang">
          <el-input v-model="ruleForm.youxiang"               placeholder="" clearable></el-input>
        </el-form-item>
        <el-form-item :style='{"width":"50%","margin":"0 0 20px 0"}'   v-if="flag=='gongcheren'"  label="" prop="dianhua">
          <el-input v-model="ruleForm.dianhua"               placeholder="" clearable></el-input>
        </el-form-item>
        <el-form-item :style='{"width":"50%","margin":"0 0 20px 0"}' v-if="flag=='gongcheren'" label="" prop="shenfenzhengzhao">
          <file-upload
