          ></file-upload>
        </el-form-item>
		<el-form-item :style='{"width":"50%","margin":"0 0 20px 0"}' v-if="flag=='users'" label="" prop="username">
			<el-input v-model="ruleForm.username" placeholder=""></el-input>
		</el-form-item>
		<el-form-item :style='{"width":"100%","padding":"0","margin":"0"}'>
			<el-button :style='{"border":"0","cursor":"pointer","padding":"0","margin":"0 20px 0 0","outline":"none","color":"rgba(255, 255, 255, 1)","borderRadius":"30px","background":"#93C7B3","width":"128px","lineHeight":"40px","fontSize":"14px","height":"40px"}' type="primary" @click="onUpdateHandler"> </el-button>
		</el-form-item>
      </el-row>
    </el-form>
  </div>
</template>
<script>
// url
import { isNumber,isIntNumer,isEmail,isMobile,isPhone,isURL,checkIdCard } from "@/utils/validate";

export default {
  data() {
    return {
      ruleForm: {},
      flag: '',
      usersFlag: false,
      yongcherenxingbieOptions: [],
      gongcherenxingbieOptions: [],
    };
  },
  mounted() {
    var table = this.$storage.get("sessionTable");
    this.flag = table;
    this.$http({
      url: `${this.$storage.get("sessionTable")}/session`,
      method: "get"
    }).then(({ data }) => {
      if (data && data.code === 0) {
        this.ruleForm = data.data;
      } else {
        this.$message.error(data.msg);
      }
    });
    this.yongcherenxingbieOptions = ",".split(',')
    this.gongcherenxingbieOptions = ",".split(',')
  },
  methods: {
    yongcherentouxiangUploadChange(fileUrls) {
        this.ruleForm.touxiang = fileUrls;
    },
    yongcherenshenfenzhengzhaoUploadChange(fileUrls) {
        this.ruleForm.shenfenzhengzhao = fileUrls;
    },
    yongcherenjiazhaoUploadChange(fileUrls) {
        this.ruleForm.jiazhao = fileUrls;
    },
    gongcherentouxiangUploadChange(fileUrls) {
        this.ruleForm.touxiang = fileUrls;
    },
    gongcherenshenfenzhengzhaoUploadChange(fileUrls) {
        this.ruleForm.shenfenzhengzhao = fileUrls;
    },
    gongcherenjiazhaoUploadChange(fileUrls) {
        this.ruleForm.jiazhao = fileUrls;
    },
    onUpdateHandler() {
      if((!this.ruleForm.yonghuming)&& 'yongcheren'==this.flag){
        this.$message.error('');
        return
      }


      if((!this.ruleForm.mima)&& 'yongcheren'==this.flag){
        this.$message.error('');
        return
      }


      if((!this.ruleForm.xingming)&& 'yongcheren'==this.flag){
        this.$message.error('');
        return
      }






        if(this.ruleForm.touxiang!=null) {
                this.ruleForm.touxiang = this.ruleForm.touxiang.replace(new RegExp(this.$base.url,"g"),"");
        }


      if( 'yongcheren' ==this.flag && this.ruleForm.nianling&&(!isIntNumer(this.ruleForm.nianling))){
       this.$message.error(``);
        return
      }
      if((!this.ruleForm.shenfenzheng)&& 'yongcheren'==this.flag){
        this.$message.error('');
        return
      }


      if( 'yongcheren' ==this.flag && this.ruleForm.shenfenzheng&&(!checkIdCard(this.ruleForm.shenfenzheng))){
        this.$message.error(``);
        return
      }


      if( 'yongcheren' ==this.flag && this.ruleForm.shouji&&(!isMobile(this.ruleForm.shouji))){
        this.$message.error(``);
        return
      }


      if( 'yongcheren' ==this.flag && this.ruleForm.youxiang&&(!isEmail(this.ruleForm.youxiang))){
        this.$message.error(``);
        return
      }
      if((!this.ruleForm.shenfenzhengzhao)&& 'yongcheren'==this.flag){
        this.$message.error('');
        return
      }


        if(this.ruleForm.shenfenzhengzhao!=null) {
                this.ruleForm.shenfenzhengzhao = this.ruleForm.shenfenzhengzhao.replace(new RegExp(this.$base.url,"g"),"");
        }
      if((!this.ruleForm.jiazhao)&& 'yongcheren'==this.flag){
        this.$message.error('');
        return
      }


        if(this.ruleForm.jiazhao!=null) {
                this.ruleForm.jiazhao = this.ruleForm.jiazhao.replace(new RegExp(this.$base.url,"g"),"");
        }




      if((!this.ruleForm.zhanghao)&& 'gongcheren'==this.flag){
        this.$message.error('');
        return
      }


      if((!this.ruleForm.mima)&& 'gongcheren'==this.flag){
        this.$message.error('');
        return
      }


      if((!this.ruleForm.xingming)&& 'gongcheren'==this.flag){
        this.$message.error('');
        return
      }






        if(this.ruleForm.touxiang!=null) {
                this.ruleForm.touxiang = this.ruleForm.touxiang.replace(new RegExp(this.$base.url,"g"),"");
        }


      if( 'gongcheren' ==this.flag && this.ruleForm.nianling&&(!isIntNumer(this.ruleForm.nianling))){
       this.$message.error(``);
        return
      }
      if((!this.ruleForm.shenfenzheng)&& 'gongcheren'==this.flag){
        this.$message.error('');
        return
      }


      if( 'gongcheren' ==this.flag && this.ruleForm.shenfenzheng&&(!checkIdCard(this.ruleForm.shenfenzheng))){
        this.$message.error(``);
        return
      }


      if( 'gongcheren' ==this.flag && this.ruleForm.youxiang&&(!isEmail(this.ruleForm.youxiang))){
        this.$message.error(``);
        return
      }


      if( 'gongcheren' ==this.flag && this.ruleForm.dianhua&&(!isMobile(this.ruleForm.dianhua))){
        this.$message.error(``);
        return
      }
      if((!this.ruleForm.shenfenzhengzhao)&& 'gongcheren'==this.flag){
        this.$message.error('');
        return
      }


        if(this.ruleForm.shenfenzhengzhao!=null) {
                this.ruleForm.shenfenzhengzhao = this.ruleForm.shenfenzhengzhao.replace(new RegExp(this.$base.url,"g"),"");
        }
      if((!this.ruleForm.jiazhao)&& 'gongcheren'==this.flag){
        this.$message.error('');
        return
      }


        if(this.ruleForm.jiazhao!=null) {
                this.ruleForm.jiazhao = this.ruleForm.jiazhao.replace(new RegExp(this.$base.url,"g"),"");
        }




      if('users'==this.flag && this.ruleForm.username.trim().length<1) {
	this.$message.error(``);
        return	
      }
      this.$http({
        url: `${this.$storage.get("sessionTable")}/update`,
        method: "post",
        data: this.ruleForm
      }).then(({ data }) => {
        if (data && data.code === 0) {
          this.$message({
            message: "",
            type: "success",
            duration: 1500,
            onClose: () => {
            }
          });
        } else {
          this.$message.error(data.msg);
        }
      });
    }
  }
};
</script>
