				zhanghao: '',
				yonghuming: '',
				xingming: '',
				shenfenzheng: '',
				shouji: '',
				shhf: '',
			},
		

			
			rules: {
				dingdanbianhao: [
				],
				cheliangmingcheng: [
				],
				cheliangleixing: [
				],
				chufadi: [
					{ required: true, message: '', trigger: 'blur' },
				],
				chepaihao: [
				],
				zairenshu: [
					{ required: true, message: '', trigger: 'blur' },
				],
				mudedi: [
					{ required: true, message: '', trigger: 'blur' },
				],
				yunhuoliang: [
					{ required: true, message: '', trigger: 'blur' },
				],
				meirizujin: [
					{ validator: validateNumber, trigger: 'blur' },
				],
				shiyongtianshu: [
					{ required: true, message: '', trigger: 'blur' },
					{ validator: validateIntNumber, trigger: 'blur' },
				],
				zongjiage: [
					{ validator: validateNumber, trigger: 'blur' },
				],
				shiyongshijian: [
				],
				zhanghao: [
				],
				yonghuming: [
				],
				xingming: [
				],
				shenfenzheng: [
					{ validator: validateIdCard, trigger: 'blur' },
				],
				shouji: [
					{ validator: validateMobile, trigger: 'blur' },
				],
				sfsh: [
				],
				shhf: [
				],
			}
		};
	},
	props: ["parent"],
	computed: {


		zongjiage:{
			get: function () {
				return 1*this.ruleForm.meirizujin*this.ruleForm.shiyongtianshu
			}
		},

	},
    components: {
    },
	created() {
		this.ruleForm.shiyongshijian = this.getCurDateTime()
	},
	methods: {
		
		// 
		download(file){
			window.open(`${file}`)
		},
		// 
		init(id,type) {
			if (id) {
				this.id = id;
				this.type = type;
			}
			if(this.type=='info'||this.type=='else'){
				this.info(id);
			}else if(this.type=='logistics'){
				this.logistics=false;
				this.info(id);
			}else if(this.type=='cross'){
				var obj = this.$storage.getObj('crossObj');
				for (var o in obj){
						if(o=='dingdanbianhao'){
							this.ruleForm.dingdanbianhao = obj[o];
							this.ro.dingdanbianhao = true;
							continue;
						}
						if(o=='cheliangmingcheng'){
							this.ruleForm.cheliangmingcheng = obj[o];
							this.ro.cheliangmingcheng = true;
							continue;
						}
						if(o=='cheliangleixing'){
							this.ruleForm.cheliangleixing = obj[o];
							this.ro.cheliangleixing = true;
							continue;
						}
						if(o=='chufadi'){
							this.ruleForm.chufadi = obj[o];
