            pageFlag : '',
			tableName:"",
			rules: {},
            yongcherenxingbieOptions: [],
            gongcherenxingbieOptions: [],
		};
	},
	mounted(){
        this.pageFlag = this.$storage.get("pageFlag");
		let table = this.$storage.get("loginTable");
		this.tableName = table;
        this.yongcherenxingbieOptions = ",".split(',')
        this.gongcherenxingbieOptions = ",".split(',')
	},
	created() {
    
	},
	destroyed() {
		  	},
	methods: {
		// uuid
		getUUID () {
			return new Date().getTime();
		},
		close(){
			this.$router.push({ path: "/login" });
		},
        yongcherentouxiangUploadChange(fileUrls) {
            this.ruleForm.touxiang = fileUrls;
        },
        yongcherenshenfenzhengzhaoUploadChange(fileUrls) {
            this.ruleForm.shenfenzhengzhao = fileUrls;
        },
        yongcherenjiazhaoUploadChange(fileUrls) {
            this.ruleForm.jiazhao = fileUrls;
        },
        gongcherentouxiangUploadChange(fileUrls) {
            this.ruleForm.touxiang = fileUrls;
        },
        gongcherenshenfenzhengzhaoUploadChange(fileUrls) {
            this.ruleForm.shenfenzhengzhao = fileUrls;
        },
        gongcherenjiazhaoUploadChange(fileUrls) {
            this.ruleForm.jiazhao = fileUrls;
        },

        // 


		// 
		login() {
			var url=this.tableName+"/register";
					if((!this.ruleForm.yonghuming) && `yongcheren` == this.tableName){
						this.$message.error(``);
						return
					}
					
					
					
					
					
					
					
					
					
					
					if((!this.ruleForm.mima) && `yongcheren` == this.tableName){
						this.$message.error(``);
						return
					}
					
					
					
					
					
					
					
					
					
					
					if((this.ruleForm.mima!=this.ruleForm.mima2) && `yongcheren` == this.tableName){
						this.$message.error(``);
						return
					}
					if((!this.ruleForm.xingming) && `yongcheren` == this.tableName){
						this.$message.error(``);
						return
					}
					
					
					
					
					
					
					
					
					
					
					
					
					
					
					
					
					
					
					
					
            if(this.ruleForm.touxiang!=null) {
                this.ruleForm.touxiang = this.ruleForm.touxiang.replace(new RegExp(this.$base.url,"g"),"");
            }
					
					
					
					
					
					
					
					
					
					
					
					
					
					if(`yongcheren` == this.tableName && this.ruleForm.nianling&&(!this.$validate.isIntNumer(this.ruleForm.nianling))){
						this.$message.error(``);
						return
					}
					
					
					
					
					
					
					
					if((!this.ruleForm.shenfenzheng) && `yongcheren` == this.tableName){
						this.$message.error(``);
						return
					}
					
					
					
					
					
					
					
					
					if(`yongcheren` == this.tableName && this.ruleForm.shenfenzheng&&(!this.$validate.checkIdCard(this.ruleForm.shenfenzheng))){
						this.$message.error(``);
						return
					}
					
					
					
					
					
					
					
					
					if(`yongcheren` == this.tableName && this.ruleForm.shouji&&(!this.$validate.isMobile(this.ruleForm.shouji))){
						this.$message.error(``);
						return
					}
					
					
					
					
					
					
					
					
					
					
					
					if(`yongcheren` == this.tableName && this.ruleForm.youxiang&&(!this.$validate.isEmail(this.ruleForm.youxiang))){
						this.$message.error(``);
						return
					}
					
					
					
            if(this.ruleForm.shenfenzhengzhao!=null) {
                this.ruleForm.shenfenzhengzhao = this.ruleForm.shenfenzhengzhao.replace(new RegExp(this.$base.url,"g"),"");
            }
					if((!this.ruleForm.shenfenzhengzhao) && `yongcheren` == this.tableName){
						this.$message.error(``);
						return
					}
					
					
					
					
					
					
					
					
					
					
            if(this.ruleForm.jiazhao!=null) {
                this.ruleForm.jiazhao = this.ruleForm.jiazhao.replace(new RegExp(this.$base.url,"g"),"");
            }
					if((!this.ruleForm.jiazhao) && `yongcheren` == this.tableName){
						this.$message.error(``);
						return
					}
					
					
					
					
					
					
					
					
					
					
					
					
					
					
					
					
					
					
					
					
					
					
					
					
					
					
					
					
					
					
				
					if((!this.ruleForm.zhanghao) && `gongcheren` == this.tableName){
						this.$message.error(``);
						return
					}
					
					
					
					
					
					
					
					
					
					
					if((!this.ruleForm.mima) && `gongcheren` == this.tableName){
						this.$message.error(``);
						return
					}
					
					
					
					
					
					
					
					
					
					
					if((this.ruleForm.mima!=this.ruleForm.mima2) && `gongcheren` == this.tableName){
						this.$message.error(``);
						return
					}
					if((!this.ruleForm.xingming) && `gongcheren` == this.tableName){
						this.$message.error(``);
						return
					}
					
					
					
					
					
					
					
					
					
					
					
					
					
					
					
					
					
					
					
					
            if(this.ruleForm.touxiang!=null) {
                this.ruleForm.touxiang = this.ruleForm.touxiang.replace(new RegExp(this.$base.url,"g"),"");
            }
					
					
					
					
					
					
					
					
					
					
					
					
					
					if(`gongcheren` == this.tableName && this.ruleForm.nianling&&(!this.$validate.isIntNumer(this.ruleForm.nianling))){
						this.$message.error(``);
						return
					}
					
					
					
					
					
					
					
					if((!this.ruleForm.shenfenzheng) && `gongcheren` == this.tableName){
						this.$message.error(``);
						return
					}
					
					
					
					
					
					
					
					
					if(`gongcheren` == this.tableName && this.ruleForm.shenfenzheng&&(!this.$validate.checkIdCard(this.ruleForm.shenfenzheng))){
						this.$message.error(``);
						return
					}
					
					
					
					
					
					
					
					
					
					if(`gongcheren` == this.tableName && this.ruleForm.youxiang&&(!this.$validate.isEmail(this.ruleForm.youxiang))){
						this.$message.error(``);
						return
					}
					
					
					
					
					
					
					
					
					
					if(`gongcheren` == this.tableName && this.ruleForm.dianhua&&(!this.$validate.isMobile(this.ruleForm.dianhua))){
						this.$message.error(``);
						return
					}
					
					
					
					
            if(this.ruleForm.shenfenzhengzhao!=null) {
                this.ruleForm.shenfenzhengzhao = this.ruleForm.shenfenzhengzhao.replace(new RegExp(this.$base.url,"g"),"");
            }
					if((!this.ruleForm.shenfenzhengzhao) && `gongcheren` == this.tableName){
						this.$message.error(``);
						return
					}
					
					
					
					
					
					
					
					
					
					
            if(this.ruleForm.jiazhao!=null) {
                this.ruleForm.jiazhao = this.ruleForm.jiazhao.replace(new RegExp(this.$base.url,"g"),"");
            }
					if((!this.ruleForm.jiazhao) && `gongcheren` == this.tableName){
						this.$message.error(``);
						return
					}
					
					
					
					
					
					
					
					
					
					
					
					
					
					
					
					
					
					
					
					
					
					
					
					
					
					
					
					
					
					
				
			
			this.$http({
				url: url,
				method: "post",
				data:this.ruleForm
			}).then(({ data }) => {
				if (data && data.code === 0) {
					this.$message({
						message: "",
						type: "success",
						duration: 1500,
						onClose: () => {
							this.$router.replace({ path: "/login" });
						}
					});
				} else {
					this.$message.error(data.msg);
				}
			});
		}
